<template>
    <div>
        <form>
            <div>
                <label>제목: </label>
                <input type="text" v-model="newQuestion.btitle" placeholder="제목을 입력하세요">
            </div>
            <div>
                <label>내용: </label>
                <textarea v-model="newQuestion.bcontent" placeholder="내용을 입력하세요"></textarea>
            </div>
            <div>
                <input type="button" @click='register' value="질문 완료">
                <input type="button" @click='reset' value="초기화">
                <input type="button" @click='gotolist' value="목록">
            </div>
        </form>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            newQuestion: {
                btitle: '',
                bcontent: '',
            }
        }
    },
    methods: {
        register() {
            axios.post("http://localhost:7777/happyhouse/qna", this.newQuestion)
            .then(() => {
                this.$router.push("/happyhouse/qna/list");
            })
            .catch((ex) => {console.log(ex)});
        },
        reset() {
            this.newQuestion.btitle = '',
            this.newQuestion.bcontent = '',
            console.log(this.newQuestion);
        },
        gotolist() {
            this.$router.push("/happyhouse/qna");
        }
    }
}
</script>