<template>
    <div>
        <form>
            <div>
                <label>제목: </label>
                <input type="text" v-model="updateQuestion.btitle" placeholder="제목을 입력하세요">
            </div>
            <div>
                <label>내용: </label>
                <textarea v-model="updateQuestion.bcontent" placeholder="내용을 입력하세요"></textarea>
            </div>
            <div>
                <input type="button" @click='modify' value="수정">
                <input type="button" @click='modify' value="취소">
                <input type="button" @click='gotolist' value="목록">
            </div>
        </form>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            updateQuestion: {
                bnum: '',
                btitle: '',
                bcontent: '',
            }
        }
    },
    created() {
        this.updateQuestion = this.$route.params.question;
    },
    methods: {
        modify() {
            console.log("여기입니다" + this.updateQuestion);
            axios.put("http://localhost:7777/happyhouse/qna", this.updateQuestion)
            .then(() => {
                this.$router.push({ name: 'Read', params : { bnum : this.updateQuestion.bnum } });
            })
            .catch((ex) => {console.log(ex)});
        },
        gotolist() {
            this.$router.push("/happyhouse/qna");
        }
    }
}
</script>